import {mapState} from 'vuex'
	export default {
		data() {
			return {
				phone_code:'', /* 输入的验证码*/
				gain: '获取验证码',
				phone:'', /* 用户输入的手机号*/
				setinter:'', /* d定时器*/
				setinter_Br:false, /*  加锁*/
			}
		},
		computed:{
			// wxUserMessage
			...mapState(['userinfo'])
		},
		methods: {
			/* 跳过 */
			async phobe_skip(){
				uni.switchTab({
					url:'../../../pages/My/My'
				})
				uni.showToast({
					title:'登录成功',
					icon:'none',
					duration:1000
				})
			},
			/* 点击完成 */
			async phobe_accomplish(){
				let telSl = /^\d{6}$/;
				let phone = this.phone
				let phone_code = this.phone_code;
				var userinfo = this.userinfo;
				
				
				
				// console.log(userinfo.unionId,userinfo.openId,userinfo.sid);
				if ( phone.length == 0 &&   phone_code.length == 0) {
					uni.showToast({
						title:'请输入手机号或者验证码',
						icon:'none'
					})
					return false;
				}
				
				let data = {
					phone,
					code:phone_code,
					unionId:userinfo.unionId,
					openId:userinfo.openId,
					sid:userinfo.sid,
					status:1
				}
				if (telSl.test(phone_code)) {
					let {data:res}   = await this.$http.get({url:'/topprogram/bind-phone',data})
					console.log(res);
						uni.showToast({
							title:res.msg,
							icon:'none',
							duration:2000
						})
						uni.setStorage({ /* 存在本地方便 以后取*/   /* 用户权限*/
							key: 'is_program_user',
							data: res.data.is_program_user,
							success: (e) => {}
						})
						uni.setStorage({ /* 存在本地方便 以后取*/
							key: 'sid',
							data: res.data.sid,
							success: (e) => {}
						})
					res.data['user_pone_Br'] = true;
					this.$store.commit('userinfoMutations',res.data)
					uni.switchTab({
						url: '/pages/My/My'
					});
				}else {
					
					uni.showToast({
						title:'请输入正确的验证码',
						icon:'none',
						duration:1000
					})
				}
				
			}, 
			// 获取短信
			async hqgain() {
				if (this.setinter_Br) {
					uni.showToast({
						title: '请勿重复点击',
						icon:'none'
					});
					return false
				}
				let telSl = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/;
				var inputStr = this.phone;
				//  这里之前还的判断 手机(及号码是否合法
				if (!(telSl.test(inputStr))) {
					uni.showToast({
						title:'请输入正确的手机号码',
						icon:'none'
					})
				}else{
					
					let data = {
						username:inputStr
					}
					let url  = '/topprogram/get-sendsms'
					let {data:res} =  await this.$http.get({url,data})
					// console.log(res);
					// if (res.msg == '发送成功') {
					
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
					if (res.succ != 1) {
						return false
					}
					let  t = 60;
					this.gain = `剩余${t}`
					this.setinter = setInterval( () => {
						t--
						this.setinter_Br = true;
						this.gain = `剩余${t}`
						if (t == 0) {
							clearInterval(this.setinter)
							this.gain = `获取验证码`
							this.setinter_Br = false;
						}
					},1000)
				}
				
			},
			/* 申请使用 */
			probation_click(){
				uni.navigateTo({
					url:'../../../pages/report/webregister/webregister'
				})
			}
		}
	}
